ent(123.04deg, #F295B3 17.11%, #C1A5D5 53.96%, #6785D2 95.33%);\n    -webkit-background-clip: text;\n    -webkit-text-fill-color: transparent;\n    /* animation: gradientAnimation 10s infinite ease-in; */\n}\n.long-text {\n    font-weight: 300;\n    font-size: 20px;\n    font-family: Roboto;\n    line-height: 30px;\n    color: #8B8B8B;\n}\n\n.big-title-container {\n    margin: 20px 0;\n}\n\n</style>"],"sourceRoot":""}]);
